import React, { useState, useEffect, useRef } from "react";
import MainSearch from "../main/MainSearch";
import { createTable } from '../../utils/tableConfig';
import { errorMsgPopup } from '../../utils/errorMsgPopup';
import styled from 'styled-components';
import styles from "./OrgSearchPopup.module.css";
import useStore from '../../store/store';
import { fetchData } from '../../utils/dataUtils';
import { updateChildrenRecursive } from '../../utils/tableUtils';
import { convertOrgInfoToHierarchy } from '../../utils/hierarchyJsonUtils';

const TableWrapper = styled.div`
  .tabulator-header .tabulator-col {
    min-height: 20px;
    line-height: 12px;
  }

  .tabulator-row {
    line-height: 12px;
  }
`;

const getMonthOptions = () => {
  const currentMonth = new Date().toISOString().slice(0, 7).replace('-', '');
  return [{ value: currentMonth, label: currentMonth, disabled: true }];
};

const OrgSearchPopup = ({ onClose, onConfirm }) => {
  const { user } = useStore();
  const [isOpen, setIsOpen] = useState(false);
  const tableRef = useRef(null);
  const tableInstance = useRef(null);
  const [filters, setFilters] = useState({ searchField: "EMPNO", mdate: new Date().toISOString().slice(0, 7).replace('-', ''), searchText: "" });
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [tableStatus, setTableStatus] = useState("initializing");
  const [hasSearched, setHasSearched] = useState(false);

  const searchConfig = {
    areas: [
      { type: "search", fields: [
        { id: "mdate", type: "select", row: 1, label: "", labelVisible: true, options: getMonthOptions(), width: "100px", height: "30px", backgroundColor: "#ffffff", color: "#000000", enabled: true },
      ]},
      { type: "buttons", fields: [
        { id: "searchBtn", type: "button", row: 1, label: "", eventType: "search", width: "80px", height: "30px", backgroundColor: "#00c4b4", color: "#ffffff", enabled: true },
      ]},
    ],
  };

  useEffect(() => {
    setIsOpen(true);
    const initializeTable = async () => {
      await new Promise((resolve) => setTimeout(resolve, 1000));
      if (!tableRef.current) {
        console.warn("   .");
        return;
      }
      try {
        tableInstance.current = createTable(tableRef.current, [
        { headerHozAlign: "center", hozAlign: "left", title: "", field: "ORGNM", sorter: "string", width: 200, frozen: true, responsive:0},
        { frozen: true, headerHozAlign: "center", hozAlign: "center", title: "", field: "select", width: 80, 
          formatter: (cell) => {
            const rowData = cell.getRow().getData();
            const seqKey = "seq";  //  
            const selectKey = "select";  //  
            const targetSeq = rowData[seqKey]; //  row seq 
            const div = document.createElement("div");
            div.style.display = "flex";
            div.style.alignItems = "center";
            div.style.justifyContent = "center";
            div.style.gap = "5px";
            const checkbox = document.createElement("input");
            checkbox.type = "checkbox";
            checkbox.checked = rowData.select === "Y";
            checkbox.onclick = (e) => {
              e.stopPropagation();
              const currentPage = tableInstance.current.getPage(); //    
              const allRows = tableInstance.current.getData(); //    

              // "select"   ( "N")
              const updatedTreeData = allRows.map(row =>
                  updateChildrenRecursive([row], seqKey, null, selectKey, "N")[0]
              ).map(row =>
                  updateChildrenRecursive([row], seqKey, targetSeq, selectKey, "Y")[0]
              );
              
              tableInstance.current.setData(updatedTreeData); //   
              tableInstance.current.setPage(currentPage); //   
            };
            const span = document.createElement("span");
            span.innerText = "";
            span.style.cursor = "pointer";
            span.onclick = (e) => {
              e.stopPropagation();
              const currentPage = tableInstance.current.getPage(); //    
              const allRows = tableInstance.current.getData(); //    

              // "select"   ( "N")
              const updatedTreeData = allRows.map(row =>
                  updateChildrenRecursive([row], seqKey, null, selectKey, "N")[0]
              ).map(row =>
                  updateChildrenRecursive([row], seqKey, targetSeq, selectKey, "Y")[0]
              );
              
              tableInstance.current.setData(updatedTreeData); //   
              tableInstance.current.setPage(currentPage); //   
            };
            div.appendChild(checkbox);
            div.appendChild(span);
            return div;
          }
        },
        { headerHozAlign: "center", hozAlign: "center", title: "", field: "seq", sorter: "number", width: 60, editable: false, formatter: (cell) => cell.getRow().getData().seq },
        { headerHozAlign: "center", hozAlign: "center", title: "", field: "ORGCD", sorter: "string", width: 100 },
        { headerHozAlign: "center", hozAlign: "center", title: "", field: "UPPERORGCD", sorter: "string", width: 120},
        { headerHozAlign: "center", hozAlign: "center", title: "", field: "ORGLEVEL", sorter: "number", width: 80, responsive:4 },
        { headerHozAlign: "center", hozAlign: "center", title: "", field: "MDATE", sorter: "string", width: 80, visible:false },
        { headerHozAlign: "center", hozAlign: "center", title: "", field: "EMPNO", sorter: "string", width: 100, visible:false },
        { headerHozAlign: "center", hozAlign: "center", title: "", field: "EMPNM", sorter: "string", width: 80, visible:false },
        { headerHozAlign: "center", hozAlign: "center", title: "", field: "OWN_ORGCD", sorter: "string", width: 120, visible:false },
        { headerHozAlign: "center", hozAlign: "left", title: "", field: "OWN_ORGNM", sorter: "string", width: 120, visible:false },
      ], [], { 
        height: '40vh', headerHozAlign: "center", headerFilter: true, layout: 'fitColumns', index: "seq", 
        dataTree: true,
        dataTreeStartExpanded: true,
        movableRows: false
      });
      if (!tableInstance.current) throw new Error("createTable returned undefined or null");

      // rowClick  
      tableInstance.current.on("rowClick", async (e, row) => {
          try {
                const rowData = row.getData(); //  row  
                const seqKey = "seq";            // Primary Key (index)
                const selectKey = "select";     //   
                const targetSeq = rowData[seqKey]; //  row seq 
                const currentPage = await tableInstance.current.getPage(); //    
                const allRows = tableInstance.current.getData(); //   

                // "select"   ( "N")
                const updatedTreeData = allRows.map(row =>
                    updateChildrenRecursive([row], seqKey, null, selectKey, "N")[0]
                ).map(row =>
                    updateChildrenRecursive([row], seqKey, targetSeq, selectKey, "Y")[0]
                );
                
                await tableInstance.current.setData(updatedTreeData); //   
                await setData(updatedTreeData); // React  
                await tableInstance.current.setPage(currentPage); //   
          } catch (error) {
              console.error("Error occurred while updating rowData on rowClick:", error);
          }
      });

        setTableStatus("ready");
      } catch (err) {
        setTableStatus("error");
        console.error("Table initialization failed:", err.message);
      }
    };
    initializeTable();
    return () => {
      if (tableInstance.current) {
        tableInstance.current.destroy();
        tableInstance.current = null;
        setTableStatus("initializing");
      }
    };
  }, []);

  useEffect(() => {
    if (tableInstance.current && tableStatus === "ready" && !loading) {
      tableInstance.current.setData(data);
      if (hasSearched && data.length === 0) {
        tableInstance.current.alert("  ", "info");
      } else {
        tableInstance.current.clearAlert();
      }
    }
  }, [data, tableStatus, loading, hasSearched]);

  const handleSearch = async () => {
    setLoading(true);
    setHasSearched(true);

    try {
        const params = {
          pGUBUN: filters.searchField || "EMPNO",
          pMDATE: filters.mdate || "",
          pSEARCH: user?.empNo || "",
          pDEBUG: "F"
        };

        const response = await fetchData("common/orginfo/list", params);
  
        if (!response.success) {
          errorMsgPopup(response.message || "    .");
          setData([]);
          return;
        }
        if (response.errMsg !== "") {
          setData([]);
          return;
        }
        const responseData = Array.isArray(response.data) ? response.data : [];
        const hierarchicalData = convertOrgInfoToHierarchy(responseData);
        let seqCounter = 1;
        const assignSeq = (nodes) => {
          return nodes.map((item) => {
            const newItem = { ...item, seq: seqCounter++, select: "N" };
            if (item._children && Array.isArray(item._children)) {
              newItem._children = assignSeq(item._children);
            }
            return newItem;
          });
        };

        const dataWithSeq = assignSeq(hierarchicalData);

        setData(dataWithSeq);
      } catch (err) {
        errorMsgPopup(err.response?.data?.message || "    .");
        setData([]);
      } finally {
        setLoading(false);
      }
  };

  const handleDynamicEvent = (eventType) => {
    if (eventType === "search") handleSearch();
  };

  const handleClose = () => {
    setIsOpen(false);
    if (onClose) onClose();
  };

  const handleConfirm = () => {
    if (onConfirm) {
      //   select === "Y"    
      const collectSelected = (nodes) => {
        let selected = [];
        nodes.forEach((node) => {
          if (node.select === "Y") {
            selected.push(node);
          }
          if (node._children && Array.isArray(node._children)) {
            selected = [...selected, ...collectSelected(node._children)];
          }
        });
        return selected;
      };

      const selectedData = collectSelected(data);
      onConfirm(selectedData); //   
    }
    handleClose();
  };

  if (!isOpen) return null;

  return (
    <div className={styles.overlay}>
      <div className={styles.popupContainer}>
        <div className={styles.header}>
          <h3> </h3>
          <button className={styles.closeButton} onClick={handleClose}>
            
          </button>
        </div>
        <div className={styles.searchSection}>
          <MainSearch config={searchConfig} filters={filters} setFilters={setFilters} onEvent={handleDynamicEvent} />
        </div>
        <TableWrapper>
          {tableStatus === "initializing" && <div className={styles.loading}> ...</div>}
          {loading && <div className={styles.loading}> ...</div>}
          <div
            ref={tableRef}
            className={styles.tableSection}
            style={{ visibility: loading || tableStatus !== "ready" ? "hidden" : "visible" }}
          />
        </TableWrapper>
        <div className={styles.buttonContainer}>
          <button className={`${styles.btn} ${styles.btnSecondary} btn btn-secondary`} onClick={handleClose}>
            
          </button>
          <button className={`${styles.btn} ${styles.btnPrimary} btn text-bg-success`} onClick={handleConfirm}>
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default OrgSearchPopup;