import React, { useState, useEffect, useRef } from 'react';
//import { fetchJsonData } from '../../utils/dataUtils';
//import sampleData from '../../data/data.json';
import { createTable } from '../../utils/tableConfig';
import { initialFilters } from '../../utils/tableEvent';
import { handleDownloadExcel } from '../../utils/tableExcel';
import useStore from '../../store/store';
import MainSearch from '../../components/main/MainSearch';
import TableSearch from '../../components/table/TableSearch';
import CommonPopup from '../../components/popup/CommonPopup';
import UserSearchPopup from '../../components/popup/UserSearchPopup';
import OrgSearchPopup from '../../components/popup/OrgSearchPopup';
import ExcelUploadPopup from '../../components/popup/ExcelUploadPopup'; // Add this line
import styles from '../../components/table/TableSearch.module.css';
import { fetchData } from '../../utils/dataUtils';
import { errorMsgPopup } from '../../utils/errorMsgPopup';

/**
 *    
 * @param {string} fieldId -  
 * @param {string} dependentValue -  
 * @returns {Array}  
 */
const getFieldOptions = (fieldId, dependentValue = '') => {
  const optionsMap = {
    status: [
      { value: '', label: '' },
      { value: 'active', label: '' },
      { value: 'inactive', label: '' },
    ],
    org1: [
      { value: '', label: '' },
      { value: 'org1', label: '' },
      { value: 'org2', label: '' },
    ],
    org2: dependentValue === 'org2' ? [
      { value: '', label: '' },
      { value: 'org21', label: '' },
      { value: 'org22', label: '' },
      { value: 'org23', label: '' },
    ] : [{ value: '', label: '' }],
    org3: dependentValue === 'org22' ? [
      { value: '', label: '' },
      { value: 'org221', label: '' },
      { value: 'org222', label: '' },
      { value: 'org223', label: '' },
    ] : [{ value: '', label: '' }],
    role: [
      { value: 'admin', label: '' },
      { value: 'user', label: '' },
    ],
    filterSelect: [
      { value: '', label: '' },
      { value: 'name', label: '' },
      { value: 'age', label: '' },
      { value: 'status', label: '' },
    ],
    orgSelect: [
      { value: '', label: '' },
      { value: 'A0001', label: '' },
      { value: 'A0002', label: '' },
    ],
  };
  return optionsMap[fieldId] || [];
};

/**
 *     
 * @returns {JSX.Element}     
 */
const TabulatorDirect = () => {
  const { user } = useStore();
  const [showPopup, setShowPopup] = useState(false);
  const [showExcelPopup, setShowExcelPopup] = useState(false); // Add this line
  const [popupTitle, setPopupTitle] = useState('');
  const [excelPopupTitle, setExcelPopupTitle] = useState(''); // Add this line
  const [popupContent, setPopupContent] = useState(null);
  const [popupOnConfirm, setPopupOnConfirm] = useState(null);
  const [selectedOrg, setSelectedOrg] = useState(user?.orgCd || ''); //    
  const [status2Options, setStatus2Options] = useState(getFieldOptions('org2'));
  const [status3Options, setStatus3Options] = useState(getFieldOptions('org3'));
  const [_selectedUsers, setSelectedUsers] = useState([]);
  const selectedOrgRef = useRef(selectedOrg); //  selectedOrg  

  // selectedOrg   ref 
  useEffect(() => {
    selectedOrgRef.current = selectedOrg;
  }, [selectedOrg]);

  //     
  const today = new Date();
  const todayDate = today.toISOString().split('T')[0];
  const todayMonth = `${today.getFullYear()}-${(today.getMonth() + 1).toString().padStart(2, '0')}`;

  //    
  // TODO: searchConfig MainSearch         .       :
  // - id:   ,  (filters)      . : 'name' filters.name  .  ,   .
  // - type:     . MainSearch   :
  //   - 'text':   (<input type="text">).  maxLength .
  //   - 'textarea':  (<textarea>). maxLength  .
  //   - 'select':  (<select>). options    .
  //   - 'day', 'startday', 'endday':    (DatePickerCommon ). 'startday' 'endday'   / ,    .
  //   - 'startmonth', 'endmonth':   . 'startmonth' 'endmonth'  / ,    .
  //   - 'dayperiod', 'monthperiod':      . { start, end }   .
  //   - 'checkbox': (<input type="checkbox">). true/false  .
  //   - 'radio':   . options    .
  //   - 'popupIcon':   (: '+'). eventType     .
  //   - 'button':  . eventType     .
  // - row:      (,  1).  row      .
  // - label:        . : ''   ":" .
  // - labelVisible:   (boolean). true()  , false .
  // - placeholder:  ('text', 'textarea', 'day', 'startday', 'endday', 'startmonth', 'endmonth', 'dayperiod', 'monthperiod')   .      label  .
  // - maxLength: 'text'  'textarea'    ( 255).    common.validateVarcharLength    .
  // - options: 'select'  'radio'    . : [{ value: 'active', label: '' }]. getFieldOptions   .
  // - eventType: 'popupIcon'  'button'      . : 'showOrgPopup'    .
  // - width:  (: '200px'). 'default'    defaultStyles.width('150px') .   '80px'.
  // - height:  (: '30px'). 'default'    defaultStyles.height('30px') .
  // - backgroundColor:  (: '#ffffff'). 'default'    defaultStyles.backgroundColor('#ffffff') .   '#00c4b4'.
  // - color:  (: '#000000'). 'default'    defaultStyles.color('#000000') .   '#ffffff'.
  // - enabled:   (boolean). true() / , false (disabled).
  // - defaultValue:  . 'day', 'startday', 'endday'  (: '2025-05-31'), 'startmonth', 'endmonth'  (: '2025-05'), 'dayperiod', 'monthperiod' { start, end } .    / .
  const searchConfig = {
    areas: [
      {
        type: 'search',
        fields: [
          { id: 'name', type: 'text', row: 1, label: '', labelVisible: true, placeholder: ' ', maxLength: 50, width: '200px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true },
          { id: 'status', type: 'select', row: 1, label: '', labelVisible: true, options: getFieldOptions('status'), width: '150px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true },
          { id: 'orgText', type: 'text', row: 2, label: '', labelVisible: true, placeholder: ' ', width: '150px', height: '30px', backgroundColor: '#f0f0f0', color: '#000000', enabled: false },
          { id: 'orgPopupBtn', type: 'popupIcon', row: 2, label: ' ', labelVisible: false, eventType: 'showOrgPopup', width: '30px', height: '30px', backgroundColor: '#f0f0f0', color: '#000000', enabled: true },
          { id: 'userText', type: 'text', row: 2, label: '', labelVisible: true, placeholder: ' ', width: '150px', height: '30px', backgroundColor: '#f0f0f0', color: '#000000', enabled: false },
          { id: 'userPopupBtn', type: 'popupIcon', row: 2, label: ' ', labelVisible: false, eventType: 'showUserPopup', width: '30px', height: '30px', backgroundColor: '#f0f0f0', color: '#000000', enabled: true },
          { id: 'testSearchBtn', type: 'button', row: 2, label: '', labelVisible: false, eventType: 'testSearch', width: '80px', height: '30px', backgroundColor: '#00c4b4', color: '#ffffff', enabled: true },
          { id: 'status1', type: 'select', row: 3, label: '', labelVisible: true, options: getFieldOptions('org1'), width: '150px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true },
          { id: 'status2', type: 'select', row: 3, label: '', labelVisible: false, options: status2Options, width: '150px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true },
          { id: 'status3', type: 'select', row: 3, label: '', labelVisible: false, options: status3Options, width: '150px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true },
          { id: 'createdDate', type: 'day', row: 4, label: '', labelVisible: true, placeholder: ' ', width: '140px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true, defaultValue: todayDate },
          { id: 'rangeStartDate', type: 'startday', row: 5, label: '()', labelVisible: true, placeholder: ' ', width: '200px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true, defaultValue: todayDate },
          { id: 'rangeEndDate', type: 'endday', row: 5, label: ' ~ ', labelVisible: true, placeholder: ' ', width: '200px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true, defaultValue: todayDate },
          { id: 'rangeStartMonth', type: 'startmonth', row: 5, label: '()', labelVisible: true, placeholder: ' ', width: '200px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true, defaultValue: todayMonth },
          { id: 'rangeEndMonth', type: 'endmonth', row: 5, label: ' ~ ', labelVisible: true, placeholder: ' ', width: '200px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true, defaultValue: todayMonth },
          { id: 'dayPeriod', type: 'dayperiod', row: 6, label: ' ', labelVisible: true, placeholder: '  ', width: '200px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true, defaultValue: { start: todayDate, end: todayDate } },
          { id: 'monthPeriod', type: 'monthperiod', row: 6, label: ' ', labelVisible: true, placeholder: '  ', width: '200px', height: '30px', backgroundColor: '#ffffff', color: '#000000', enabled: true, defaultValue: { start: todayMonth, end: todayMonth } },
          { id: 'isActive', type: 'checkbox', row: 7, label: ' ', labelVisible: true, width: 'default', height: 'default', backgroundColor: 'default', color: 'default', enabled: true },
          { id: 'role', type: 'radio', row: 8, label: ' ', labelVisible: true, options: getFieldOptions('role'), width: 'default', height: 'default', backgroundColor: 'default', color: 'default', enabled: true },
        ],
      },
      {
        type: 'buttons',
        fields: [
          { id: 'searchBtn', type: 'button', row: 1, label: '', eventType: 'search', width: '80px', height: '30px', backgroundColor: '#00c4b4', color: '#ffffff', enabled: true },
          { id: 'resetBtn', type: 'button', row: 8, label: '', eventType: 'reset', width: '80px', height: '30px', backgroundColor: '#00c4b4', color: '#ffffff', enabled: true },
          { id: 'popupBtn2', type: 'button', row: '8', label: ' ', eventType: 'showPopup', width: '100px', height: '30px', backgroundColor: '#00c4b4', color: '#ffffff', enabled: true },
          { id: 'excelUploadBtn', type: 'button', row: '8', label: '', eventType: 'showExcelUploadPopup', width: '100px', height: '30px', backgroundColor: '#00c4b4', color: '#ffffff', enabled: true },
          { id: 'tempUploadBtn', type: 'button', row: '9', label: 'common      comfirm   (ExcelUploadTemplateManage.jsx)', eventType: 'showTempUploadPopup', width: '680px', height: '30px', backgroundColor: '#00c4b4', color: '#ffffff', enabled: true },
        ],
      },
    ],
  };

  const filterTableFields = [
    { id: 'filterSelect', type: 'select', label: '', options: getFieldOptions('filterSelect'), width: 'default', height: 'default', backgroundColor: 'default', color: 'default', enabled: true },
    { id: 'filterText', type: 'text', label: '', placeholder: '  ', width: 'default', height: 'default', backgroundColor: 'default', color: 'default', enabled: true },
  ];

  // const [filters, setFilters] = useState(initialFilters(searchConfig.areas.find((area) => area.type === 'search').fields));
  const [filters, setFilters] = useState({...initialFilters(searchConfig.areas.find((area) => area.type === 'search').fields),    orgText: user?.orgNm || '', });
  const [tableFilters, setTableFilters] = useState(initialFilters(filterTableFields));
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [isSearched, setIsSearched] = useState(false);
  const [tableStatus, setTableStatus] = useState('initializing');
  const [error, setError] = useState(null);
  const [rowCount, setRowCount] = useState(0);
  const tableRef = useRef(null);
  const tableInstance = useRef(null);
  const isInitialRender = useRef(true);
  const latestFiltersRef = useRef(filters);

  //   ref     
  useEffect(() => {
    latestFiltersRef.current = filters;
  }, [filters]);

  //   
  const columns = [
    { title: 'ID', field: 'ID', width: 80, headerHozAlign: 'center', hozAlign: 'center' },
    { title: '', field: 'NAME', width: 150, headerHozAlign: 'center', hozAlign: 'center' },
    { title: '', field: 'AGE', sorter: 'number', headerHozAlign: 'center', hozAlign: 'center' },
    { title: '', field: 'STATUS', headerHozAlign: 'center', hozAlign: 'center' },
    { title: '', field: 'REGDT', headerHozAlign: 'center', hozAlign: 'center' },
    { title: '', field: 'STARTTIME', headerHozAlign: 'center', hozAlign: 'center' },
    { title: '', field: 'ENDTIME', headerHozAlign: 'center', hozAlign: 'center' },
  ];

  //   
  /**
   * JSON        
   * @async
   */
  const loadData = async () => {
    setLoading(true);
    setIsSearched(true);
    setError(null);

    //   
    await new Promise((resolve) => setTimeout(resolve, 0));

    //   
    const currentFilters = latestFiltersRef.current;

    // API    fetchData()
    try {
      const params = {pGUBUN: 'LIST', pNAME: currentFilters.name || '',pSTATUS: currentFilters.status || '', pDEBUG: "F"};

      const response = await fetchData("sample/tabulator/list", params);
      if (!response.success) {
        errorMsgPopup(response.message || "    .");
        setData([]);
        return;
      }
      if (response.errMsg !== "") {
        console.log(response.errMsg);
        setData([]);
        return;
      }
      const responseData = Array.isArray(response.data) ? response.data : [];
      setData(responseData);
    } catch (err) {
      errorMsgPopup(err.response?.data?.message || "    .");
      setData([]);
    } finally {
      setLoading(false);
    }

    // JSON    fetchJsonData()
    /*
    try {
      const params = {
        name: currentFilters.name || '',
        status: currentFilters.status || '',
      };

      const result = await fetchJsonData(sampleData, params);

      //   
      const dataArray = Array.isArray(result) ? result : [result];

      //    
      const filteredData = dataArray.filter(item =>
        (!params.name || item.name === params.name) &&
        (!params.status || item.status === params.status)
      );

      setData(filteredData);
    } catch (err) {
      setData([]);
      setError('  : ' + err.message);
    } finally {
      setLoading(false);
    }
      */
  };
  

  //   
  /**
   * , ,     
   * @param {string} eventType -  
   * @param {Object} eventData -  
   */
  const handleDynamicEvent = (eventType, eventData) => {
    if (eventType === 'search') {
      loadData();
    } else if (eventType === 'reset') {
      setFilters(initialFilters(searchConfig.areas.find((area) => area.type === 'search').fields));
      setData([]);
      setIsSearched(false);
      setSelectedOrg('');
      setStatus2Options(getFieldOptions('org2'));
      setStatus3Options(getFieldOptions('org3'));
      setSelectedUsers([]); // Added: Reset selected users on reset
    } else if (eventType === 'showPopup') {
      setPopupTitle('');
      setPopupContent(`ID: ${eventData.id} `);
      setPopupOnConfirm(() => () => {
        setShowPopup(false);
        return true;
      });
      setShowPopup(true);
    } else if (eventType === 'showOrgPopup') {
      setPopupTitle(' ');
      setPopupContent(
        <div>
          <OrgSearchPopup
            onClose={() => setShowPopup(false)}
            onConfirm={(selectedRows) => {
              const orgNames = selectedRows.map(row => row.ORGNM).join(', ');
              setSelectedOrg(selectedRows.length > 0 ? selectedRows[0].ORGCD : '');
              setFilters((prev) => ({ ...prev, orgText: orgNames || '' }));
              console.log('Selected Organizations in TabulatorDirect:', selectedRows); //   
            }}
          />
        </div>
      );
      setPopupOnConfirm(() => () => {
        setShowPopup(false);
        return true;
      });
      setShowPopup(true);
    } else if (eventType === 'showUserPopup') {
      setPopupTitle(' ');
      setPopupContent(
        <div>
          <UserSearchPopup
            onClose={() => setShowPopup(false)}
            onConfirm={(selectedRows) => {
              setSelectedUsers(selectedRows);
              const userNames = selectedRows.map(row => row.EMPNM).join(', ');
              setFilters((prev) => ({ ...prev, userText: userNames || '' }));
              console.log('Selected Users:', selectedRows);
            }}
          />
        </div>
      );
      setPopupOnConfirm(() => () => {
        setShowPopup(false);
        return true;
      });
      setShowPopup(true);
    }
    else if (eventType === 'testSearch') {
      setPopupTitle('');
      setPopupContent('  .');
      setPopupOnConfirm(() => () => {
        setShowPopup(false);
        return true;
      });
      setShowPopup(true);
    } else if (eventType === 'showExcelUploadPopup') {
      setExcelPopupTitle('');
      setShowExcelPopup(true);
    } else if (eventType === 'selectChange') {
      const { id, value } = eventData;
      if (id === 'status1') {
        setStatus2Options(getFieldOptions('org2', value));
        setStatus3Options(getFieldOptions('org3'));
        setFilters((prev) => ({ ...prev, status2: '', status3: '' }));
      } else if (id === 'status2') {
        setStatus3Options(getFieldOptions('org3', value));
        setFilters((prev) => ({ ...prev, status3: '' }));
      }
    }
  };

  // Tabulator  
  /**
   * Tabulator  ,    
   * @async
   */
  useEffect(() => {
    const initializeTable = async () => {
      //    
      await new Promise((resolve) => setTimeout(resolve, 1000));
      if (!tableRef.current) {
        console.warn("   .");
        return;
      }
      try {
        // Tabulator  
        //1.    
        //tableInstance.current = createTable(tableRef.current, columns, [], {});
        //2.    
        //tableConfig.js  defaultOptions   override  
        tableInstance.current = createTable(tableRef.current, columns, [], {
          headerHozAlign: "center",
          headerFilter: true,
          layout: 'fitColumns'
        });

        if (!tableInstance.current) throw new Error("createTable returned undefined or null");
        setTableStatus("ready");

        //3.    
        //rowClick, rowDblClick, cellClick, cellDblClick,   ....
        tableInstance.current.on("rowClick", (e, row) => {
          const rowData = row.getData();
          console.log("rowClick:", rowData);
        });

        tableInstance.current.on("cellDblClick", (e, cell) => {
          console.log("cellDblClick:", cell.getField(), cell.getValue());
        });

        //4.    UserSearchPopup.jsx   
        //tableInstance.current.updateData       .


      } catch (err) {
        setTableStatus("error");
        console.error("  :", err.message);
      }
    };

    initializeTable();

    //     
    return () => {
      if (tableInstance.current) {
        tableInstance.current.destroy();
        tableInstance.current = null;
        setTableStatus("initializing");
      }
    };
  }, []);

  //  
  /**
   *   ,     
   */
  useEffect(() => {
    if (isInitialRender.current) {
      isInitialRender.current = false;
      return;
    }
    const table = tableInstance.current;
    if (!table || tableStatus !== "ready" || loading) return;
    if (table.rowManager?.renderer) {
      table.setData(data);
      if (isSearched && data.length === 0 && !loading) {
        tableInstance.current.alert("  ", "info");
      } else {
        tableInstance.current.clearAlert();
        const rows = tableInstance.current.getDataCount();
        setRowCount(rows);
      }
    } else {
      console.warn("renderer   .");
    }
  }, [data, loading, tableStatus, isSearched]);

  //   
  /**
   *    
   */
  useEffect(() => {
    if (isInitialRender.current || !tableInstance.current || tableStatus !== "ready" || loading) return;
    const { filterSelect, filterText } = tableFilters;
    if (filterText && filterSelect) {
      tableInstance.current.setFilter(filterSelect, "like", filterText);
    } else if (filterText) {
      if (filterText !== "") {
        tableInstance.current.setFilter(
          [
            { field: "MENUNM", type: "like", value: filterText },
            { field: "URL", type: "like", value: filterText },
            { field: "USEYN", type: "like", value: filterText },
          ],
          "or"
        );
      } else {
        tableInstance.current.clearFilter();
      }
    } else if (filterSelect) {
      tableInstance.current.clearFilter();
    }
  }, [tableFilters.filterSelect, tableFilters.filterText, tableStatus, loading]);

  return (
    <div className={styles.container}>
      <MainSearch
        config={searchConfig}
        filters={filters}
        setFilters={setFilters}
        onEvent={handleDynamicEvent}
      />
      <TableSearch
        filterFields={filterTableFields}
        filters={tableFilters}
        setFilters={setTableFilters}
        onDownloadExcel={() => handleDownloadExcel(tableInstance.current, tableStatus, '.xlsx')}
        rowCount={rowCount}
        onEvent={handleDynamicEvent}
      />
      <div className={styles.tableWrapper}>
        {tableStatus === 'initializing' && <div> ...</div>}
        {loading && <div> ...</div>}
        {error && <div>{error}</div>}
        <div
          ref={tableRef}
          className={styles.tableSection}
          style={{ visibility: loading || tableStatus !== 'ready' ? 'hidden' : 'visible' }}
        />
      </div>
      <CommonPopup
        show={showPopup}
        onHide={() => setShowPopup(false)}
        onConfirm={popupOnConfirm}
        title={popupTitle}
      >
        {popupContent}
      </CommonPopup>
      <ExcelUploadPopup
        show={showExcelPopup}
        onHide={() => setShowExcelPopup(false)}
        onSave={(result) => {
          if (result.errCd === '00') {
            loadData(); // Refresh table on success
          }
          return result;
        }}
        title={excelPopupTitle}
        rptCd="exceluploadsample"
        templateParams={{ pGUBUN: 'RPTCD', pTITLE: '', pFILEID: '', pRPTCD: 'exceluploadsample', pDEBUG: 'F' }}
      />
    </div>
  );
};

export default TabulatorDirect;