/**
 * Tabulator        
 * @param {Array} data -    (    )
 * @param {String} seqKey -     seq  
 * @param {String} targetSeq -   seq  ( )
 * @param {String} selectKey -   () 
 * @param {String|Number} newSelectValue -  select   
 * @returns {Array} -    
 */
export const updateChildrenRecursive = (data, seqKey, targetSeq, selectKey, newSelectValue) => {
    return data.map(row => {
        const updatedRow = {
            ...row,
            [selectKey]: row[seqKey] === targetSeq ? newSelectValue : "N", //     ,  "N"
        };

        if (row._children) {
            updatedRow._children = updateChildrenRecursive(row._children, seqKey, targetSeq, selectKey, newSelectValue);
        }

        return updatedRow;
    });
};
